<template>
  <div>
    <span class="dropdown-menu dropdown-menu-right" aria-labelledby="settings" style="font-weight: 400;">
      <span class="dropdown-item dropdown-item-menu-title">
        <i class="fas fa-cog post"></i>
        <label>Post Actions</label>
        <span class="dropdown-item" v-if="account_id === user.userID" @click="showModal()">
          <i class="fas fa-pencil-alt pencil"></i>
          <label style="cursor: pointer;">Edit</label>
        </span>
        <span class="dropdown-item" v-if="account_id !== user.userID" @click="report()">
          <i class="fas fa-file-alt file"></i>
          <label style="cursor: pointer;">Report</label>
        </span>
        <span class="dropdown-item" v-if="account_id === user.userID" @click="showRemove(id)">
          <i class="fas fa-trash-alt trash"></i>
          <label style="color: red; cursor: pointer;">Delete</label>
        </span>
      </span>
    </span>
    <div class="modal fade" id="editPost" tabindex="-1" role="dialog" aria-labelledby="exampleModalLongTitle" aria-hidden="true" v-if="forEdit">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLongTitle">Edit Post</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="grow-wrap">
              <textarea onInput="this.parentNode.dataset.replicatedValue = this.value" class="input-post" v-model="forEdit.text" :placeholder="'Update your post'"/>
            </div>
            <PostImage :images="forEdit.images"/>
            <button class="text-center sort-button" @click="edit()">Update</button>
          </div>
        </div>
      </div>
    </div>
    <Confirmation
    ref="confirm"
    :title="'Confirmation'"
    :message="'Are you sure you want to delete this event?'"
    @onConfirm="remove($event)"
    />
  </div>
</template>
<script>
import AUTH from 'src/services/auth'
import Confirmation from 'src/components/increment/generic/modal/Confirmation.vue'
import PostImage from 'src/modules/generic/EditImages.vue'
export default{
  props: ['id', 'account_id', 'text', 'images'],
  data(){
    return {
      user: AUTH.user,
      forEdit: null
    }
  },
  components: {
    Confirmation,
    PostImage
  },
  methods: {
    showRemove(id) {
      this.$refs.confirm.show(id)
    },
    report() {
      let parameter = {
        payload: 'comment_id',
        payload_value: this.id,
        status: 'ongoing',
        account_id: this.user.userID
      }
      $('#loading').css({display: 'block'})
      this.APIRequest('reports/create', parameter).then(response => {
        $('#loading').css({display: 'none'})
      })
    },
    edit() {
      if(this.text === null || this.text === '') {
        return
      }
      let parameter = {
        id: this.id,
        text: this.text
      }
      $('#loading').css({display: 'block'})
      this.APIRequest('comments/update', parameter).then(response => {
        $('#loading').css({display: 'none'})
        if(response.data > 0) {
          this.$parent.updateText(this.text)
          $('#editPost').modal('hide')
          this.text = null
        }
      })
    },
    remove(id){
      let parameter = {
        id: id
      }
      $('#loading').css({display: 'block'})
      this.APIRequest('comments/delete', parameter, response => {
        $('#loading').css({display: 'none'})
        if(response.data > 0) {
          this.$parent.removePost(id)
        }
      })
    },
    showModal() {
      $('#editPost').modal('show')
    }
  }
}
</script>
<style scoped lang="scss">
@import "~assets/style/colors.scss";
.dropdown-menu.show{
  position: absolute;
  margin: 10px -25px;
}
.grow-wrap{
  display: grid;
}
.grow-wrap::after {
  /* Note the weird space! Needed to preventy jumpy behavior */
  content: attr(data-replicated-value) " ";
  /* This is how textarea text behaves */
  white-space: pre-wrap;
  /* Hidden from view, clicks, and screen readers */
  visibility: hidden;
}
.grow-wrap > textarea {
  /* You could leave this, but after a user resizes, then it ruins the auto sizing */
  resize: none;
  /* Firefox shows scrollbar on growth, you can hide like this. */
  overflow: hidden;
}
.grow-wrap > textarea,
.grow-wrap::after {
  /* Identical styling required!! */
  outline: none;
  font: inherit;
  /* Place on top of each other */
  grid-area: 1 / 1 / 2 / 2;
}
.sort-button{
  margin-top: 10px;
  border-radius: 25px;
  width: 120px;
  color: white;
  border: 0px;
  height: 40px;
  background-color: $primary;
  float: right;
}
.input-post{
  border-style: none;
  outline: none;
  width: 100%;
}
.dropdown-menu{
  margin-top: 40px;
  width: 240px;
  min-height: 125px;
  padding-bottom: 0px !important;
}
.dropdown-item .trash{
  color: red;
}
.dropdown-item{
  width: 100% !important;
  height: 40px !important;
  float: left !important;
  background: #fff !important;
  padding-top: 0px !important;
  cursor: pointer !important;
}
.dropdown-item i{
  padding-right: 10px !important;
}
.dropdown-item-menu-title label{
  margin-bottom: 0px;
  line-height: 40px;
  font-weight: 550;
}
.dropdown-item-menu-title:hover, .dropdown-item-menu-title label:hover{
  cursor: default;
  background: #fff !important;
}

</style>



